<template>
    <el-form :model="config.dataSource"
             :rules="rules"
             size="mini"
             label-width="80px"
             ref="dbSourceForm">
        <el-form-item label="域名/IP" prop="host">
            <el-input v-model="config.dataSource.host"></el-input>
        </el-form-item>
        <el-form-item label="端口" prop="port">
            <el-input v-model="config.dataSource.port"></el-input>
        </el-form-item>
        <el-form-item label="用户名" prop="userName">
            <el-input v-model="config.dataSource.userName"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password">
            <el-input v-model="config.dataSource.password"></el-input>
        </el-form-item>
        <el-form-item label="数据库" prop="db">
            <el-col :span="16">
                <el-select v-model="config.dataSource.db"
                           placeholder="请选择数据库">
                    <el-option v-for="(item, i) in db"
                               :key="item"
                               :label="item"
                               :value="item">
                    </el-option>
                </el-select>
            </el-col>
            <el-col :span="7" :offset="1">
                <el-button size="mini" type="primary" plain @click="initDb">获取数据库列表</el-button>
            </el-col>
        </el-form-item>
    </el-form>
</template>

<script>
    import CONST from '../../../config/const'
    import API from '../../../config/api'

    export default {
        name: "DatabaseForm",
        props: ['config'],
        data() {
            return {
                rules: {
                    host: [{required: true, message: '必须填写'}],
                    port: [{required: true, message: '必须填写'}],
                    userName: [{required: true, message: '必须填写'}],
                    password: [{required: true, message: '必须填写'}],
                    db: [{required: true, message: '必须填写'}],
                },
                db: []
            }
        },
        mounted() {
            if (this.config && this.config.dataSource.id && this.config.dataSource.id) {
                this.initDb()
            }
        },
        methods: {
            /**
             * 获得数据库列表
             */
            initDb() {
                this.db = []
                let ds = this.config.dataSource
                this.ajax({
                    url: API.DATA_SOURCE.LIST_DB,
                    method: CONST.AJAX.POST,
                    data: {
                        host: ds.host,
                        port: ds.port,
                        userName: ds.userName,
                        password: ds.password,
                        type: ds.type
                    },
                }).then(response => {
                    this.db = response.data
                })
            },
        }
    }
</script>

