<template>
    <div>
        <el-form-item label="url" required>
            <el-input v-model="config.dataSource.url"></el-input>
        </el-form-item>
        <el-form-item label="协议" required>
            <el-select v-model="config.dataSource.protocol"
                       placeholder="请选择协议类型">
                <el-option v-for="(item, i) in protocol"
                           :key="item.name"
                           :label="item.name"
                           :value="item.name">
                </el-option>
            </el-select>
        </el-form-item>
    </div>
</template>

<script>
    export default {
        name: "ApiForm",
        props: ['config'],
        data() {
            return {
                protocol: [
                    {name: "http"},
                    {name: "https"},
                ]
            }
        }
    }
</script>

