<template>
    <el-dialog width="40%"
               :title="config.dataSource.id ? '编辑数据源' : '新建数据源'"
               :visible.sync="config.visible"
               :close-on-click-modal="false">
        <el-form :model="config.dataSource"
                 :rules="rules"
                 size="mini"
                 label-width="80px"
                 ref="dataSourceForm">
            <el-form-item label="名称" prop="name">
                <el-input v-model="config.dataSource.name"></el-input>
            </el-form-item>
            <el-form-item label="类型">
                <el-select v-model="config.dataSource.type"
                           placeholder="请选择数据源类型"
                           :default-first-option="true">
                    <el-option v-for="item in dataSourceType"
                               :key="item.type"
                               :label="item.desc"
                               :value="item.type">
                    </el-option>
                </el-select>
            </el-form-item>
        </el-form>
        <component :is="form.loader[form.mapper[config.dataSource.type]].default"
                   ref="subDsForm"
                   :config="config"></component>
        <div slot="footer" class="dialog-footer">
            <el-button size="mini" type="primary" @click="save">确 定</el-button>
            <el-button size="mini" @click="config.visible = false">取 消</el-button>
        </div>
    </el-dialog>
</template>

<script>
    import API from '../../config/api'
    import CONST from '../../config/const'
    import _ from 'lodash'

    export default {
        name: "DataSourceEdit",
        props: ['config'],
        data() {
            return {
                rules: {
                    name: [
                        {required: true, message: '必须填写名称', trigger: 'blur'}
                    ]
                },
                form: {
                    mapper: {},
                    url: {},
                    loader: {}
                },
                dataSourceType: [{
                    type: 'EXCEL',
                    desc: 'XLS/CSV 文件',
                    form: 'FileForm',
                    url: API.DATA_SOURCE.SAVE_FILE,
                }, {
                    type: 'POSTGRESQL',
                    desc: 'PostgreSQL数据库',
                    form: 'DatabaseForm',
                    url: API.DATA_SOURCE.SAVE_DATABASE,
                }, {
                    type: 'MYSQL',
                    desc: 'MySQL数据库',
                    form: 'DatabaseForm',
                    url: API.DATA_SOURCE.SAVE_DATABASE,
                }, {
                    type: 'ORACLE',
                    desc: 'Oracle数据库',
                    form: 'DatabaseForm',
                    url: API.DATA_SOURCE.SAVE_DATABASE,
                }, {
                    type: 'SQLSERVER',
                    desc: 'SqlServer数据库',
                    form: 'DatabaseForm',
                    url: API.DATA_SOURCE.SAVE_DATABASE,
                }]
            }
        },
        created() {
            this.loadForm()
            // 新建时默认选中Excel文件
            if (!this.config.dataSource) {
                this.config.dataSource = _.assignIn({}, this.dataSourceType[0])
            }
        },
        methods: {
            /**
             * 保存数据源
             */
            save() {
                let dsForm = this.$refs.dataSourceForm
                let subForm = this.$refs.subDsForm
                dsForm.validate((res, obj) => {
                    if (!res) {
                        return
                    }
                    subForm.$children[0].validate((res, obj) => {
                        if (!res) {
                            return
                        }
                        let url = this.form.url[this.config.dataSource.type]
                        this.ajax({
                            url: url,
                            method: CONST.AJAX.POST,
                            data: this.config.dataSource,
                        }).then(response => {
                            if (response.success) {
                                this.$message(CONST.MESSAGE.SAVE_SUCCESS)
                                this.config.visible = false
                            }
                        })
                    })
                })
            },
            /**
             * 动态加载表单
             */
            loadForm() {
                for (let i = 0; i < this.dataSourceType.length; i++) {
                    let item = this.dataSourceType[i]
                    let form = item.form
                    this.form.mapper[item.type] = form
                    this.form.url[item.type] = item.url
                    if (this.form.loader[form]) {
                        continue
                    }
                    this.form.loader[form] = require("../../components/datasource/form/" + form + ".vue")
                }
            },
        }
    }
</script>

